#drum-machine {
	height: 100vh;
	background-color: black;
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-align: center;
	align-items: center;
}

h1 {
	font-family: 'Satisfy', cursive;
	margin: 0;
	color: #30e7ed;
	font-size: 5rem;
}

#control-panel {
	background-color: #30e7ed;
	width: 500px;
	height: 500px;
}

#display {
	font-family: 'Bebas Neue', cursive;
	color: #30e7ed;
	font-size: 3rem;
	height: 70px;
	width: 435px;
	margin: 25px auto;
	background-color: black;
	padding-top: 15px;
	padding-bottom: 15px;
}

#pad-container {
	display: grid;
	grid-template-rows: 1fr 1fr 1fr;
	grid-template-columns: 1fr 1fr 1fr;
	grid-gap: 10px;
	justify-items: center;
}
.drum-pad {
	font-family: 'Metal Mania', cursive;
	font-size: 2rem;
	background-color: black;
	color: #30e7ed;
	width: 100px;
	height: 100px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 10px;
}
